@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;500&display=swap);
body, html {
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

.start-button{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: rgb(34, 161, 30);
    cursor: pointer;
    outline: none;
    margin-bottom: 20px;
    border-radius: 100px;
    border: none;
    color: white;
}

.btn-disabled .power-icon{
    color: rgba(255, 255, 255, 0.582);
}

.btn-disabled{
    cursor: not-allowed;
    pointer-events: none;
    background-color: rgb(27, 90, 25);
}

.start-button:hover{
    background-color: rgb(34, 150, 30);
}

.main-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.reactor-panel-container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.number-victories{
    position: fixed;
    top: 20px;
    right: 10px;
    font-size: 1.5rem;
}
.output-container{
    position: relative;
    width: 50vh;
    height: 50vh;
    border-radius: 50px;
    background-color: rgb(177, 177, 177);
    display: flex;
    align-items: center;
    justify-content: center;
}

.output-screen{
    width: 80%;
    height: 75%;
    display: grid;
    grid-template-columns: 0.8fr 0.8fr 0.8fr;
    grid-template-rows: 0.8fr 0.8fr 0.8fr;
    background-color: black;
}

.lights-container{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 8px;
    width: 80%;
}
.square{
    transition: 0.2s all;
    width: 100%;
    height: 100%;
}

.isActivated{
    animation: blink 1s;
    animation-fill-mode: forwards;
    background-color: rgb(0, 140, 255);
}

.isNormal{
    opacity: 0;
    background: none;
}

@keyframes blink{
    0%{
        opacity: 0;
    }

    50%{
        opacity: 1;
    }

    100%{
        opacity: 0;
    }
}

.circle{
    width: 25px;
    height: 25px;
    margin-right: 5px;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.404);
}

.is-enabled{
    background-color: #07cf11;
}

.is-disabled{
    background-color: #004d00;
}
  @media (min-width: 768px) and (max-width: 1024px) {
    .circle{
        width: 20px;
        height: 20px;
    }
  }
  
  
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) { 
    .circle{
        width: 15px;
        height: 15px;
    }
  }
  
  @media (min-width: 481px) and (max-width: 767px) {
    .circle{
        width: 15px;
        height: 15px;
    }
    
  }
  
  @media (min-width: 320px) and (max-width: 480px) {
    .circle{
        width: 10px;
        height: 10px;
    }
  }
.input-container{
    width: 50vh;
    height: 50vh;
    border-radius: 50px;
    margin-left: 20px;
    background-color: rgb(177, 177, 177);
    display: flex;
    align-items: center;
    justify-content: center;
}

.input-buttons-container{
    width: 80%;
    height: 75%;
    display: grid;
    grid-template-columns: 0.8fr 0.8fr 0.8fr;
    grid-template-rows: 0.8fr 0.8fr 0.8fr;
}
.basic-button{
    width: 100%;
    height: 100%;
    outline: none;
    background-color: gray;
}

.basic-btn-disabled{
    background-color: rgb(90, 90, 90);
    cursor: not-allowed;
    pointer-events: none;
}

.basic-btn-defeat{
    animation-name: blink-red;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    cursor: not-allowed;
    pointer-events: none;
}

.basic-button:hover{
    cursor: pointer;
    background-color: rgb(110, 110, 110);
}

@keyframes blink-red{
    0%{
        background-color: rgb(110, 110, 110);
    }

    25%{
        background-color: rgba(255, 0, 0, 0.795);
    }

    50%{
        background-color: rgb(110, 110, 110);
    }

    75%{
        background-color: rgba(255, 0, 0, 0.795);
    }

    100%{
        background-color: rgb(90, 90, 90);
    }
}
.victory-container{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    position: fixed;
    top: 0;
    z-index: 500;
    background-color: #6cc070;
}

.victory-container h1{
    font-size: 3em;
}

.victory-container button{
    padding: 20px;
    margin-bottom: 9px;
    font-weight: bold;
    border: none;
    outline: none;
    width: 180px;
    background-color: white;
    color: black;
}

.victory-container button:hover{
    background-color: rgb(235, 235, 235);
    cursor: pointer;
}
